
import Box from 'vux/src/components/box/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Icon from 'vux/src/components/icon/index.vue'


export default {
  created () {
    let self = this
    self.load()
  },
  components: {
    Box,
    Card,
    Group,
    Cell,
    Icon
  },
  methods: {
    load () {
      let self = this
      let $http = this.$http
      $http.post(`a/api/msgDetail/list`, {}).then(res => {
        let response = res.body
        let list = response.results.list
        self.list = list
      })
    }
  },
  data () {
    return {
      title: '',
      list: []
    }
  }
}



