
import _ from 'lodash'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'


const storage = window.localStorage
const RESETTYPE = 2

export default {
  created () {
    let self = this
    let userInfo = JSON.parse(storage.getItem('userInfo'))
    _.merge(self.userInfo, userInfo)
  },
  components: {
    XInput,
    XButton,
    Group,
    Cell
  },
  methods: {
    reset () {
      const self = this
      const $http = this.$http
      const $router = this.$router
      const alert = this.$vux.alert
      let fields = [
        'mobile',
        'oldPassword',
        'password',
        'refPassword'
      ]
      let data = _.merge({
        userId: self.userInfo.id,
        resetType: RESETTYPE
      }, _.pick(self.$data, fields))
      $http.put(`a/api/reset/${self.userInfo.id}`, data).then(res => {
        let response = res.body
        alert.show({
          title: '提示',
          content: response.status.info
        })
        $router.go(-1)
      })
    }
  },
  data () {
    return {
      title: '',
      oldPassword: '',
      password: '',
      refPassword: '',
      userInfo: {}
    }
  }
}



