
import _ from 'lodash'
import Group from 'vux/src/components/group/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'


const storage = window.localStorage

export default {
  created () {
    let self = this
    let userInfo = JSON.parse(storage.getItem('userInfo'))
    self.integral = userInfo.dlUserAccount.integral
    self.load()
  },
  components: {
    Group,
    Grid,
    GridItem,
    Cell,
    Icon,
    Tab,
    TabItem,
    Swiper,
    SwiperItem
  },
  methods: {
    load () {
      let self = this
      let $http = this.$http
      let fields = [
        'integralType',
        'recordType'
      ]
      let data = {}
      _.merge(data, _.pick(self.$data, fields))
      if (data.integralType === 0) {
        delete data.integralType
      }
      if (data.recordType === 2) {
        delete data.integralType
      } else {
        delete data.recordType
      }
      $http.post(`a/api/integralLog/list`, data).then(res => {
        let response = res.body
        let list = response.results.list
        self.list = list
      })
    },
    change (index, old) {
      let self = this
      self.integralType = parseInt(index)
      if (self.integralType === 5) {
        self.recordType = 2
      } else {
        self.recordType = 1
      }
      self.load()
    },
    height () {
      return `${this.list.length * 55}px`
    }
  },
  computed: {
    height: () => {
      if (this.list) {
        return `${this.list.length * 55}px`
      } else {
        return '800px'
      }
    }
  },
  data () {
    return {
      title: '',
      index: 0,
      integral: 0,
      tabs: {
        0: '全部',
        1: '小票积分', // 小票
        2: '签到积分',
        3: '电商积分',
        // 4: '积分兑换'
        5: '积分兑换'
      },
      dict: {
        1: '小票积分',
        2: '签到积分',
        3: '电商积分',
        4: '抽奖'
      },
      type: {
        1: '收入',
        2: '支出'
      },
      list: [],
      integralType: 0,
      recordType: 1
    }
  },
  watch: {
    'index': 'change'
  }
}



