import { mapState } from 'vuex'

export default {
  name: 'app',
  computed: {
    ...mapState({
      route: state => state.route,
      path: state => state.route.path,
      deviceready: state => state.app.deviceready,
      direction: state => state.navigation.direction
    })
  }
}



