
import _ from 'lodash'
import Box from 'vux/src/components/box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

const USERTYPE = 1
const storage = window.localStorage

export default {
  components: {
    Box,
    Group,
    Cell,
    Icon,
    XInput,
    XButton
  },
  methods: {
    sigin () {
      const self = this
      const $http = this.$http
      const router = this.$router
      const alert = this.$vux.alert
      let fields = [
        'mobile',
        'password'
      ]
      let data = _.merge({
        userType: USERTYPE
      }, _.pick(self.$data, fields))
      $http.post('a/api/login', data).then(res => {
        let response = res.body
        let userInfo = response.results
        if (response.status.index === '10000') {
          storage.setItem('token', response.token)
          storage.setItem('userInfo', JSON.stringify(userInfo))
          router.push('me')
        } else {
          alert.show({
            title: '提示',
            content: response.status.info
          })
        }
      })
    }
  },
  data () {
    return {
      mobile: '',
      password: ''
    }
  }
}



