
import _ from 'lodash'
import Box from 'vux/src/components/box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Countdown from 'vux/src/components/countdown/index.vue'

const COUNT_TIME = 80
const USERTYPE = 1
const SENDTYPE = 1

export default {
  components: {
    Box,
    Group,
    Cell,
    Icon,
    XInput,
    XButton,
    Countdown
  },
  methods: {
    next () {
      let self = this
      if (!self.start) {
        self.start = true
      } else {
        self.step += 1
      }
    },
    finish (index) {
      let self = this
      self.start = false
      self.time = COUNT_TIME
    },
    sigup () {
      const self = this
      const $http = this.$http
      const router = this.$router
      let fields = [
        'mobile',
        'code',
        'password'
      ]
      let data = _.merge({
        userType: USERTYPE
      }, _.pick(self.$data, fields))
      $http.post('a/api/regist', data).then(res => {
        let response = res.body
        if (response.status.index === '10000') {
          router.push('sigin')
        } else {
          self.alert(response.status.info)
        }
      })
    },
    getAuthCode () {
      const self = this
      const $http = this.$http
      let fields = [
        'mobile'
      ]
      let data = _.merge({
        userType: USERTYPE,
        secret: '1i9PivUeseAuThorDL17',
        sendType: SENDTYPE
      }, _.pick(self.$data, fields))
      if (!self.start) {
        self.start = true
        $http.post(`a/api/smsAuthCode/${self.mobile}`, data, res => {})
      } else {
        self.step += 1
      }
    },
    alert (message) {
      const alert = this.$vux.alert
      alert.show({
        title: '提示',
        content: message
      })
    }
  },
  data () {
    return {
      mobile: '',
      password: '',
      code: '',
      repassword: '',
      step: 0,
      time: COUNT_TIME,
      start: false
    }
  }
}



