
import Box from 'vux/src/components/box/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Icon from 'vux/src/components/icon/index.vue'

import moment from 'moment'

export default {
  created () {
    let self = this
    self.load()
  },
  components: {
    Box,
    Card,
    Group,
    Cell,
    Icon
  },
  methods: {
    load () {
      let self = this
      let $http = this.$http
      $http.post(`a/api /bill/list`, {}).then(res => {
        let response = res.body
        let list = response.results.list
        for (let i = 0; i < list.length; i++) {
          list[i].createDate = moment(list[i].createDate).format('YYYY-MM-DD HH:mm')
        }
        self.list = list
      })
    }
  },
  data () {
    return {
      title: '',
      list: [],
      status: {
        1: '审核通过',
        2: '审核中',
        3: '审核失败'
      }
    }
  }
}



