
import _ from 'lodash'
import Blur from 'vux/src/components/blur/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'

import { mapState } from 'vuex'

const storage = window.localStorage

export default {
  created () {
    let self = this
    self.loadInfo()
  },
  components: {
    Blur,
    Group,
    Card,
    Grid,
    GridItem
  },
  methods: {
    loadInfo () {
      let self = this
      let $http = this.$http
      let userInfo = JSON.parse(storage.getItem('userInfo'))
      $http.post(`a/api/user/${userInfo.id}`, {}).then(res => {
        let response = res.body
        let info = response.results
        let fields = [
          'nickName',
          'name',
          'sex',
          'birthday',
          'mobile',
          'email'
        ]
        _.merge(self.userInfo, info)
        _.merge(self.userInfo, _.pick(info.dlUserInfo, fields))
        storage.setItem('userInfo', JSON.stringify(self.userInfo))
      })
    },
    onUpdate (val, old) {
      let self = this
      let $store = this.$store
      console.log(val)
      if (val) {
        self.loadInfo()
        $store.commit('user:update', {updated: false})
      }
    },
    sign () {
      let self = this
      let $http = this.$http
      $http.post(`a/api/onsign`, {}).then(res => {
        let resData = res.body
        self.alert(resData.status.info)
        if (resData.status.index === '10000') {
          self.loadInfo()
        }
      })
    },
    alert (message) {
      const alert = this.$vux.alert
      alert.show({
        title: '提示',
        content: message
      })
    }
  },
  computed: {
    ...mapState({
      updated: state => state.user.updated
    })
  },
  data () {
    return {
      // note: changing this line won't causes changes
      // with hot-reload because the reloaded component
      // preserves its current state and we are modifying
      // its initial state.
      bg: {
        backgroundImage: `url(static/bg.png);`
      },
      msg: 'Hello World!',
      bgUrl: 'static/bg.png',
      userInfo: {
        nickName: '',
        name: '',
        sex: '',
        birthday: '',
        mobile: '',
        email: '',
        isSign: false,
        msgCount: 0,
        dlUserAccount: {}
      }
    }
  },
  watch: {
    'updated': 'onUpdate'
  }
}



