
import _ from 'lodash'
import XAddress from 'vux/src/components/x-address/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import ChinaAddressV3Data from 'vux/src/datas/china_address_v3.json'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'


const parseArea = (code) => {
  let no = parseInt(code)
  return [`${parseInt(no / 1000) * 1000}`, `${parseInt(no / 100) * 100}`, `${no}`]
}

export default {
  mounted () {
    let self = this
    let $route = this.$route
    let $http = this.$http
    self.id = $route.params['id']
    if (isNaN(self.id)) {
      $route.meta.title = '编辑地址'
      $http.post(`a/api/address/${self.id}`).then(res => {
        let results = res.body.results
        let area = parseArea(results.area.id)
        results.isDefault = results.isDefault === '1'
        _.merge(self.$data, results)
        self.zipcode = results.zipCode
        self.area = area
      })
    } else {
      $route.meta.title = '新增地址'
      self.isDefault = parseInt(self.id) === 0
    }
  },
  components: {
    XAddress,
    XInput,
    XButton,
    XSwitch,
    Group,
    Cell,
    Icon,
    Selector,
    Datetime
  },
  methods: {
    submit () {
      let self = this
      let $http = this.$http
      let $router = this.$router
      let $store = this.$store
      let fields = [
        'id',
        'name',
        'mobile',
        'areaId',
        'zipcode',
        'detailed',
        'isDefault'
      ]
      let data = _.merge({}, _.pick(self.$data, fields))
      data.isDefault = data.isDefault ? 1 : 0
      console.log(self.area)
      data.areaId = self.area[2]
      if (!isNaN(self.id)) {
        $http.post(`a/api/address`, data).then(res => {
          let response = res.body
          self.alert(response.status.info)
          $router.go(-1)
          $store.commit('update', {updated: true})
        })
      } else {
        $http.put(`a/api/address/${self.id}`, data).then(res => {
          let response = res.body
          self.alert(response.status.info)
          $router.go(-1)
          $store.commit('update', {updated: true})
        })
      }
    },
    alert (message) {
      const alert = this.$vux.alert
      alert.show({
        title: '提示',
        content: message
      })
    }
  },
  data () {
    return {
      title: '',
      id: '',
      name: '',
      mobile: '',
      areaId: '',
      zipcode: '',
      detailed: '',
      defaultValue: '',
      isDefault: false,
      area: [],
      addressData: ChinaAddressV3Data
    }
  }
}



