
import { mapState } from 'vuex'
import XNumber from 'vux/src/components/x-number/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Icon from 'vux/src/components/icon/index.vue'


export default {
  beforeDestroy () {
    let $store = this.$store
    $store.commit('selected', {data: {}})
  },
  components: {
    XNumber,
    XButton,
    Card,
    Group,
    Cell,
    Icon
  },
  methods: {
    select () {
      let self = this
      let $router = this.$router
      self
      $router.push({name: 'Address', params: { select: true }})
    }
  },
  computed: {
    ...mapState({
      address: state => state.address.data
    })
  },
  data () {
    return {
      title: ''
    }
  }
}



